<template>
  <div class="body">
    <div class="todoBody">
      <div
        @click="mutarEdesmutarMusica"
        v-bind:class="{ soundMuted: comMusica }"
      >
        <img src="../../img/music.png" alt="" />
        <audio :muted="comMusica" loop autoplay="true">
          <source
            :src="require(`../../fundoMusical/${path}`)"
            type="audio/mp3"
          />
        </audio>
      </div>
    </div>
    <h1>Escala de {{ this.notaRecebida }}</h1>
    <div class="escala row">
      <div
        class="acordes"
        @click="removerNota"
        v-for="item in escalaDoUsuario"
        :key="item"
      >
        <div :class="isNull" role="status" v-if="item.length == 0"></div>
        <div v-else-if="item.length != 0">
          {{ item }}
        </div>
      </div>
    </div>
    <!-- <div class="escala row"  >
            <div class="acordes" @click="removerNota" v-for="item in triade" :key="item">
                <div :class="isNull"  role="status" v-if="item.length == 0">
                </div>
                <div v-else-if="item.length != 0">
                    {{ item }}
                </div>
            </div>
            
        </div> -->
    <!-- Button trigger modal -->
    <button
      type="button"
      @click="verificaSeAEscalaTaCorreta"
      class="btn btn-primary"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal"
    >
      Conferir a Escala
    </button>

    <!-- Modal -->
    <div
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h1 class="modal-title fs-5" id="exampleModalLabel">Resultado</h1>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            {{ this.resultado }}
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary"
              data-bs-dismiss="modal"
            >
              Fechar
            </button>
            <!-- <button type="button" class="btn btn-primary">Ok</button> -->
          </div>
        </div>
      </div>
    </div>
    <router-link class="router navbar-brand" :to="{ name: 'home' }"
      ><button class="btn btn-warning" variant="success" :to="{ name: 'home' }">
        Voltar ao início
      </button></router-link
    >

    <Notas :notas="notas" :counter="counter" />
  </div>
</template>

<script>
import Notas from "./Notas.vue";
export default {
  name: "Escala",
  data() {
    return {
      escalaDoUsuario: ["", "", "", "", "", "", ""],
      triade: ["", "", ""],
      escala: [],
      isNull: "spinner-grow",
      notas: [
        "C",
        "C#",
        "D",
        "D#",
        "E",
        "F",
        "F#",
        "G",
        "G#",
        "A",
        "A#",
        "B",
        "Db",
        "Eb",
        "Gb",
        "Ab",
        "Bb",
      ],
      notasComBemol: {
        "C#": "Db",
        "D#": "Eb",
        "F#": "Gb",
        "G#": "Ab",
        "A#": "Bb",
      },
      controle: [],
      counter: 0,
      notaRecebida: "",
      path:
        localStorage.getItem("fundo") == null
          ? "Baixo.mp3"
          : localStorage.getItem("fundo") + ".mp3",
      comMusica: false,
      resultado: "",
    };
  },
  components: {
    Notas,
  },
  methods: {
    removerNota(e) {
      let nota = e.target.innerHTML;
      if (nota != "") {
        //Remove qualquer nota que o usuario clicar da escala "EscalaDoUsuario"
        for (let i = 0; i < this.escalaDoUsuario.length; i++) {
          if (nota == this.escalaDoUsuario[i]) {
            this.controle.push(i);
            this.notas.push(this.escalaDoUsuario[i]);
            this.counter -= 1;
            this.escalaDoUsuario[i] = "";
          }
        }
      }
    },
    verificaVazioEAdiciona(nota) {
      //Quando o usuario retira uma nota da escala, ele deixa um lugar vazio. Essa função verifica esse lugar e preenche com a próxima nota que ele apertar.
      this.controle.sort((a, b) => a - b);
      for (let i = 0; i < this.controle.length; i++) {
        if (this.escalaDoUsuario[this.controle[i]] == "") {
          this.escalaDoUsuario[this.controle[i]] = nota;
          break;
        }
      }
    },
    adicionaEverifica(from) {
      //Aqui ele pega as notas que usuario está clicando e adiciona na lista escala do usuario que é a escala que tem de ser preeenchida
      if (this.escalaDoUsuario[from[1]] == "") {
        this.escalaDoUsuario[from[1]] = from[0];
        this.counter += 1;
      } else {
        this.verificaVazioEAdiciona(from[0]);
      }
    },
    verificaSeAEscalaTaCorreta() {
      //Função para verificar a escala do usuario, se esta tudo correto.
      let listaDeNotasErradas = [];
      let preenchida = true;
      for (let i = 0; i < this.escalaDoUsuario.length; i++) {
        if (this.escalaDoUsuario[i] != this.escala[i]) {
          listaDeNotasErradas.push(this.escalaDoUsuario[i]);
        }
        if (this.escalaDoUsuario[i] == "") {
          this.resultado = "A escala não foi totalmente preenchida ainda.";
          preenchida = false;
          break;
        }
      }
      if (preenchida) {
        //Verificando se a escala foi totalmente preenchida
        if (listaDeNotasErradas.length > 0) {
          this.resultado =
            "As seguintes notas estão erradas: " + listaDeNotasErradas + "";
        } else {
          this.resultado = "Parabéns, você acertou todas as notas da escala!!";
        }
      }
    },
    mudaEscala() {
      let escolhaEscala = this.notaRecebida; //Essa variavel vai ser mudada pelo usuario

      //essa função vai gerar as escalas que o usuario desejar, Por enquanto so a escala maior
      let A = "Bb";
      let C = "Db";
      let D = "Eb";
      let F = "Gb";
      let G = "Ab";

      let controle = 0;
      let controle2 = 0;
      if (this.escala.length <= 0) this.escala.push(escolhaEscala);

      for (let i = 0; i < this.notas.length; i++) {
        controle2 = i;
        //Ele verifica a nota que o usuario escolheu, e ai quando a nota que ele escolheu for igual na escala, ele vai gerar uma escala apartir da posição que essa nota se encontra na lista
        if (this.notas[i] == escolhaEscala) {
          for (let a = 0; a < 7; a++) {
            //Esse if verifica se ja tem coisa na escala, caso o usuario ficasse apertando gerar escala
            if (this.escala.length <= 6) {
              if (controle + controle2 >= 11) {
                //Quando ele chega na ultima nota, para não colocar Bmol na lista ele volta para o inicio da lista
                controle2 = -1;
                controle = 0;
              }
              if (this.notaRecebida == "F") {
                if (controle + controle2 >= 10) {
                  //Quando ele chega na ultima nota, para não colocar Bmol na lista ele volta para o inicio da lista
                  controle2 = -2;
                  controle = 0;
                }
              }
              //Aqui ele faz toda a parte de Tom, tom e semitom
              if (a == 1 || a == 2 || a == 4 || a == 5 || a == 6) {
                controle += 2;
                this.escala.push(this.notas[controle + controle2]);
              }
              if (a == 3) {
                controle += 1;
                this.escala.push(this.notas[controle + controle2]);
                // if (this.notaRecebida == "F") {
                //     controle = 0
                //     controle2 = -1
                // }
              }
            }
          }
        }
      }
      for (let i = 0; i < this.escala.length; i++) {
        if (this.escala[this.escala.length - 1] != this.escala[i]) {
          //Aqui ele verifica as notas sustenidas(#) que estão se repetindo e substitui por bmol
          // console.log(this.escala[this.escala.length - 1] + "\n\n"+ this.escala[i])
          if (this.escala[i] == this.escala[i + 1].split("")[0]) {
            this.escala[i + 1] = this.notasComBemol[this.escala[i + 1]];
          }
        }
      }
    },
    recuperandoDadosLocalStorage() {
      if (localStorage.getItem("nota") != null) {
        this.notaRecebida = localStorage.getItem("nota");
      } else {
        this.notaRecebida = "C";
      }
      // if(localStorage.getItem("fundo") != null){
      //     // this.path = localStorage.getItem("fundo") + '.mp3'
      // }
      this.mudaEscala();
    },
    mutarEdesmutarMusica() {
      if (this.comMusica) {
        this.comMusica = false;
        console.log("Esse é o novo path: " + this.path);
      } else {
        this.comMusica = true;
      }
    },
    trocarMusica(arquivo) {
      if (localStorage.getItem("fundo") != null) {
        this.path = localStorage.getItem("fundo") + ".mp3";
        localStorage.removeItem("fundo");
      }
      this.path = arquivo + ".mp3";
    },
  },
  mounted() {
    //recebendo a nota que o usuario apertou
    this.$emitter.on("nota", (from) => (this.notaRecebida = from));
    this.recuperandoDadosLocalStorage();
    this.$emitter.on("fundo", (from) => this.trocarMusica(from));
    this.$emitter.on("escala", (from) =>
      from[1] != 7
        ? this.adicionaEverifica(from)
        : this.verificaVazioEAdiciona(from[0])
    );
  },
};
</script>



<style scoped>
h1 {
  color: black;
}
.todoBody {
  /* background: red; */
  width: 100%;
  height: 50px;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: end;
  align-items: center;
  padding-right: 30px;
}
.todoBody .soundMuted {
  cursor: pointer;
  position: relative;
  /* background: black; */
}
#app > div > div.todoBody > div {
  cursor: pointer;
}
.todoBody .soundMuted::after {
  content: "";
  position: absolute;
  width: 2px;
  height: 28px;
  background-color: black;
  /* border-radius: 16px; */
  transform: rotate(45deg) translateY(0px);
  left: 8px;
  animation-name: linha;
  animation-duration: 0.5s;
}
.body {
  width: 100%;
  min-height: 92.5vh;
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(53, 227, 167);
  background: linear-gradient(
    135deg,
    rgba(53, 227, 167, 0.896796218487395) 15%,
    rgba(53, 167, 227, 1) 79%
  );
}
.body .escala {
  max-width: 800px;
  width: 100%;
  min-height: 70px;
  border: 1px solid black;
  border-radius: 16px;
  padding: 4px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  justify-content: center;
  align-items: center;
}
.body .escala .acordes {
  width: 60px;
  height: 60px;
  border: 1px solid black;
  color: white;
  background: #333;
  font-weight: bold;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
  margin: 6px 3px 0 3px;
}
.body .escala .acordes:hover {
  box-shadow: 3px 5px rgb(255, 255, 255);
}
.btn {
  margin: 10px 0 30px 0;
}

.spinner-grow {
  --bs-spinner-animation-speed: 1.5s;
}
@keyframes linha {
  0% {
    transform: rotate(45deg) translateY(-10px);
  }
  100% {
    transform: rotate(45deg) translateY(0);
  }
}
</style>